                         _this.envioNotificaciones(mensajenotifica,'Actualización de Observación de un Seguimiento de una Actividad',mensajecorreo)
                        
                        _this.dialog_vobosubdir = false

                    }else{	                                         	

                        _this.showMessage(true,3000,'error',res.message) 	                  	
                    }                                    
                    
            }).catch(function(err) {

                _this.showMessage(true,3000,'error',err)                          	
                
            })

        },
        
        abrirDialogoElimFile(tipo, item){
            this.tipoarchivo = tipo
            this.itemactualArchivo = item
            this.dialog_deletefile = true
        },
        abrirDialogoAbrirArchivo(tipo, edit){
            this.tipoarchivo = tipo
            this.itemactualArchivo = ''
            this.editArchivos = edit
            this.codigoarchivo = 0				
            this.rutaarchivo = ''					
            this.archivoactual = {file_name:'',file_path:'',file_name_tmp:''}
            this.dialog_archivos = true
        },
        resetArchivos(){
            this.codigoarchivo = 0				
            this.rutaarchivo = ''					
            this.archivoactual = {file_name:'',file_path:'',file_name_tmp:''}				
        },
        async eliminarItemArchivos(){

            try {

                const result = await proyectossegdetService.eliminarArchivoXTipo(this.codigo,this.anno,this.mes, this.tipoarchivo,this.itemactualArchivo.consecutivo,this.itemactualArchivo.archivo)
                
                if(result.response) {

                    this.showMessage(true,4000,'error',result.response.data.message)
                    return							
                }

            } catch (error) {
                
                this.showMessage(true,4000,'error',error)
                return
            }

            this.traerDatosArchivos(this.tipoarchivo)
            /*
            switch (this.tipoarchivo) {
                case 'ATS':
                    
                    for (let index = 0; index < this.dataarcats.length; index++) {
                        const element = this.dataarcats[index]

                        if(element.consecutivo == this.itemactualArchivo.consecutivo ){
                            
                            this.dataarcats.splice( index, 1 )
                            this.showMessage(true,4000,'success','Se eliminó el archivo con éxito')
                            break
                        }
                    }

                    break

                case 'AAF':
                    
                    for (let index = 0; index < this.dataarcaaf.length; index++) {
                        const element = this.dataarcaaf[index]

                        if(element.consecutivo == this.itemactualArchivo.consecutivo){
                            
                            this.dataarcaaf.splice( index, 1 )
                            this.showMessage(true,4000,'success','Se eliminó el archivo con éxito')
                            break
                        }
                    }

                    break

                default:
                    break
            }*/

            this.dialog_deletefile = false

        },

        async adicionarArchivo(){
            
            var f = new Date()

            var fechaformat = f.getFullYear().toString().padStart(2, '00') + '-' + (f.getMonth() +1).toString().padStart(2, '00') + '-' + f.getDate().toString()
            
            if(this.archivoactual.file_name == '') {

                this.showMessage(true,3000,'error', 'Debe seleccionar el archivo')
                return
            }

            if(this.editArchivos == 0){

                var max = 0

                //se busca los consecutivos
                switch (this.tipoarchivo) {
                    case 'ATS':
                        
                        max = this.dataarcats.reduce((maximo, element) => Math.max(maximo, element.codigo), 0)
                        max += 1

                        break

                    case 'AAF':
                        
                        max = this.dataarcaaf.reduce((maximo, element) => Math.max(maximo, element.codigo), 0)
                        max += 1

                        break

                    default:

                        max = -1
                        break
                }
                
                //se debe agregar si esta en modo edición, si es nuevo al guardar se envían los datos
                if(parseFloat(this.codigo) != 0){

                    try {

                        const result = await proyectossegdetService.adicionarArchivo(this.codigo,this.anno,this.mes,this.tipoarchivo,max,this.archivoactual,this.observacionarchivo,this.fechaarchivo)
                        
                        if(result.response) {

                            this.showMessage(true,4000,'error',result.response.data.message)
                            return							
                        }

                    } catch (error) {
                        
                        this.showMessage(true,4000,'error',error)
                        return
                    }
                }

                this.traerDatosArchivos(this.tipoarchivo)
                /*switch (this.tipoarchivo) {
                    case 'ATS':							

                        this.dataarcats.push({codigo:max,archivo:this.archivoactual,fecha:this.fechaarchivo,descripcion: this.observacionarchivo})

                        break

                    case 'AAF':
                        
                        this.dataarcaaf.push({codigo:max,archivo:this.archivoactual,fecha:this.fechaarchivo,descripcion: this.observacionarchivo})

                        break

                    default:

                        max = -1
                        break
                }*/

                this.showMessage(true,4000,'success','Se agregó el archivo con éxito')

            }

            this.observacionarchivo = ''
            this.fechaarchivo = ''
            this.editArchivos = -1				
            this.dialog_archivos = false
        },

        traerDatosArchivos(tipo){            
            var _this = this				
            
            if (tipo == 'ATS')
                this.dataarcats = []
            else    
                this.dataarcaaf = []

            proyectossegdetService.getDataArchivos(this.codigo,this.anno,this.mes, tipo)
                .then(function(res) {

                    if (res.request) {
                                    
                        if(res.data){
                        
                        let dataactual = res.data.data
                        
                        if(dataactual.length > 0){

                            for (let j = 0; j < dataactual.length; j++) {
                                const element = dataactual[j];

                                let archivoc = element.archivo
                                let rutasarchivo = element.rutaarchivo

                                let arc = {file_name:archivoc,file_path:(rutasarchivo != '' ? rutasarchivo : ''),file_name_tmp:''}									

                                switch (tipo) {
                                    case 'ATS':                                         
                                        _this.dataarcats.push({consecutivo:element.consecutivo,codigo:element.codigo,archivo:arc,fecha:element.fecha,descripcion:element.observacion})	
                                        break

                                    case 'AAF':
                                        _this.dataarcaaf.push({consecutivo:element.consecutivo,codigo:element.codigo,archivo:arc,fecha:element.fecha,descripcion:element.observacion})	
                                        break

                                    default:
                                        break;
                                }
                                                                
                            }
                        }                            
                        }

                    }                
                    
            }).catch(function(err) {               
                
            })
        },

        agregaBitacora(accion, detalle){              

            var data = {modulo:'SEGUIMIENTO PLAN DE ACCION PROYECTOS',accion:accion,detalle:detalle}
            
            this.$root.$emit('agregaBitacora', data)

        },
    },

    watch: {

        proyecto: function (val) {

            if(val!=0){   
                
                this.traerDataProyecto()               

            }            
        },

        codigo: function (val) {

            if(val!=0){
                
                this.traerData()               

            }            
        },
        
        anno: function (val) {

            if(val!=0){
                
                this.traerData()               

            }            
        },

        mes: function (val) {

            if(val!=0){

                this.devolverMes()
                this.traerData()               

            }            
        },
    },

    mounted() {

        this.traerDataProyecto()
        this.traerData()
        this.devolverMes()
        this.traeUsuarios()
        
    },

}

</script>

